.book_book-text-cont{

    width: 75%;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
  
}
.book_text_item-cont{
    overflow-y: auto;
    height: 70vh;
    margin-top: 40px;
    width: 100%;
}
.book_number-ch{
    color: #fff;
    margin-top: 10px;
    font-size: 18px;
    font-weight: 700;
}
.book_title-ch{
    color: #fff;
    margin-top: 10px;
    font-size: 20px;
    font-weight: 700;
}
.book_text_item{
    color: #fff;
    margin-top: 10px;
   
}
.book_edit-img{
    width: 30px;
}
.book_edit-img-cont{
    width: 40px;
    height: 30px;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

}
.book_edit-img-cont:hover{
    background-color: #850685;
}
.book_title-container{
    display: flex;
    align-items: center;
    height: 50px;
    position: relative;
    width: 100%;
    justify-content: center;
    
}
.book_edit-img-cont{
    position: absolute;
    right: 15px;
    top: 10px;
}
.bool_text-item-cont{
    display: flex;
    position: relative;
    width: 100%;

}
.book_text_item{
    width: 90%;

}
.book_text-area-title{
    border: 1px solid #fff;
    background-color: transparent;
    border-radius: 5px;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 500px;
    font-size: 20px;
    font-weight: 700;
    margin-top: 40px;
    height: 40px;
    outline: none;
   
}
.book_btn{
    padding: 5px;
    color: #fff;
    border: 1px solid #fff;
    font-size: 14px;
    margin-left: 10px;
    border-radius: 5px;
    cursor: pointer;

}
.book_btn:hover{
    background-color: #850685;
}
.book_btn-cont{
    display: flex;
    justify-content: flex-end;
    margin-top: 10px;
 
}
.book_btn-input-cont{
    display: flex;
    justify-content: flex-end;
    margin-top: 10px;
    width: 90%;
}
.book_text-input{
    width: 90%;
    background-color: transparent;
    outline: none;
    color: #fff;
    height: 400px;
    border:1px solid #fff;
    border-radius: 5px;
    font-size: 18px;
}
.book_text-item-input-cont{
    display: flex;
    width: 100%;
    flex-direction: column;
}
.book_content-btn{
  height: 200px;
  width: 200px;
display: flex;
justify-content: space-around;
align-items: center;
    border:1px solid #fff;
    color: #fff;
    cursor: pointer;
    border-radius: 5px;
    margin-top: 15px;
    font-size: 20px;
    margin-left: 10px;
    flex-direction: column;

    
}
.book_content-btn:hover{
    background-color: #850685;
}
.edit-book_settings{
    color: #fff;
    margin-top: 200px;
    font-size: 20px;
    font-weight: 700;
}
.book_content-btn-cont{
    width: 90%;
    display: flex;
    justify-content: flex-end;
    margin-top: 15px;

}
.book_gen-img-cont{
    display: flex;
    flex-direction: row;
    width: 90%;
    flex-wrap: wrap;

}
.book_gen-img{
    margin-left: 10px;
    cursor: pointer;
    margin-top: 15px;
}
.edit-book_select-cont{
    width: 80%;
    height: 40px;
    margin-top: 15px;
}
.info-btn_cont{
    color: #fff;

    
    height: 40px;
  
    

    font-size: 18px;
    font-weight: 700;
    margin-top: 50px;
    cursor: pointer;
}

.book_back-btn{
    color: #fff;
    border:1px solid #fff;
    width: 200px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    font-size: 18px;
    font-weight: 700;
    margin-top: 50px;
    cursor: pointer;
    position: absolute;
    right: 10px;
    top: -15px;
}
.book_back-btn:hover{
    background-color: #850685;
}
.info-container{
    border:1px solid #fff;
    color: #fff;
    display: flex;
    flex-direction: column;
    height: 200px;
    width: 300px;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    margin-left: 10px;
}
.info{
    display: flex;
    flex-wrap: wrap;
    margin-top:75px;
}