.selected-component_container{
    width: 90%;
    height: 88vh;
    background-color: rgba(0, 0, 0, 0.8);
    position: relative;
  

}

.selected-component_steps-item{
    color:#fff;
    font-size: 16px;
    font-weight: 700;
    width: 200px;
    margin-top: 20px;
    cursor: pointer;

}
.selected-component_steps-list{
    display: flex;
    width: 350px;
    margin-left: 2.5%;
    margin-top: 10px;
   flex-direction: column;
   height: 90vh;
   height: 80vh;
   overflow-y: auto;
   



}
.selected-component_steps-item.active{
   color: #850685;
   border-bottom: 3px solid #850685;
}
.selected-component_title{
    color: #fff;
    margin-top: 15px;
    
}
.selected-component_desc{
    color: #fff;
    height: 25px;
    
}
.selected-component_selected-item{
    color: #fff;
    border:1px solid #fff;
    padding: 20px;
    border-radius: 5px;
    margin-top: 10px;
    cursor: pointer;
    width: 26%;
    margin-left: 3%;
    margin-top: 20px;
}

.selected-component_selected-container{
overflow-y: auto;
   height: 65vh;
  display: flex;
  flex-wrap: wrap;
  

}
.selected-component_selected-item:hover{
background-color: #850685;
}
.selected-component_text-bold{
    font-weight: 900;
    padding-bottom: 5px;
    margin-top: 10px;
}
.selected-component_selected-checkbox{
    width: 20px;
    height: 20px;
    background-color: #fff;
    border-radius: 4px;
    border:3px solid #fff
}
.selected-component_selected-checkbox.active{
    background-color: #850685;
}
.selected-component_selected-block{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.selected-component_selected-text{
    font-size:14px;
    text-align: center;
}
.selected-component_btn{
    height: 40px;
    width: 100px;
    border: 1px solid #fff;
    border-radius: 6px;
    color: #fff;
    justify-content: center;
    align-items: center;
    display: flex;
}
.selected-component_btn:hover{
    background-color: #850685;
    cursor: pointer;
}
.selected-component_btn-cont{
    display: flex;
    width: 90%;
    margin-left: 5%;
    justify-content: flex-end;
    position: absolute;
    bottom: 20px;


}
.selected-component_indent{
    width: 10px;
    
}
.selected-component_input-cont{
    display: flex;
    justify-content: center;
}
.selected-component_input{
    all: unset;
width: 700px;
height: 53vh;
background-color: transparent;
outline: none;
border:1px solid #fff;
border-radius:10px;
color:#fff;
padding-top: 20px;
padding-left: 20px;
}
.selected-component_input::placeholder{
    color: #fff;
}
.selected-component_selected-item.active{
background-color: green;
}
.wrapper{
    display: flex;
    justify-content: center;
    padding-top: 72px;
}
.loader-container{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 200px;
}
.loader{
    width: 800px;
    height: 50px;
    background-color: #fff;
    border-radius: 10px;
    

}
.loader-title{
    color: #fff;
}

@media (max-height: 900px) {
    .selected-component_selected-container {
     height: 57vh;
    }
    .selected-component_selected-item{
        margin-left: 2%;
    }
  }
  