.header_container{
    width: 90%;
    margin-left: 5%;
    height: 70px;
    background-color: rgba(0, 0, 0, 0.8);
    /* background-color: #850685; */
    position: absolute;
    display: flex;
    align-items: center;
    border: 1px solid #fff;
    border-radius: 3px;
    
}
.header_items-block{
    display: flex;
    width: 95%;
    justify-content: flex-end;
    align-items: center;
}
.header_item{
    margin-left: 15px;
    color: #fff;
    font-size: 20px;
    font-weight: 700;
    cursor: pointer;
    transition: 0.4s;
}
.header_item:hover{
    color: #850685;
    transition: 0.4s;
}
.header_item-img{
    margin-left: 15px;
    width: 40px;
    cursor: pointer;
}
.header_item-cent-cont{
    display: flex;  
    align-items: center;
    margin-left: 30px;
}
.header_item-cent{
    color: #fff;
    margin-left: 5px;
    font-weight: 600;
}
.header_item-cent-img{
    width: 30px;
    
}