.select-template_input{
    border: 2px solid #fff;
    background-color: transparent;
    width: 500px;
    height: 50px;
    border-radius: 5px;
    padding-left: 10px;
    color: #fff;
    outline: none;
    font-size: 18px;
}
.select-template_input-cont{
    display: flex;
    justify-content: center;
 
}
.select-template_input::placeholder{
    color: #fff;
    font-size: 18px;
}
.select-template_header{
    color: #fff;
    text-align: center;
    font-size: 26px;
    margin-top: 50px;
}
.select-template_list-item{
    width: 200px;
    height: 300px;
    border: 2px solid #fff
}
.select-template_list-cont{
    width: 80%;
    display: flex;
    justify-content: center;
    margin-left: 10%;
}
.select-template{
    border: 2px solid #fff;
    color: #fff;
    width: 200px;
    height: 200px;
    display: flex;
    justify-content: center;
    margin-left: 15px;
    cursor: pointer;
    border-radius: 5px;
}
.select-template:hover{
    background-color: #850685;
}
.select-template.active{
    background-color: green;
}
.select-template_btn{
    width: 200px;
    height: 50px;
    border: 2px solid #fff;
    color: #fff;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    font-weight: 700;
}
.select-template_btn-cont{
    display: flex;
    justify-content: center;
    margin-top: 50px;
    cursor: pointer;
}
.select-template_btn:hover{
background-color: #850685;
}