.first-screen_text{
    color: #fff;
    font-size: 28px;
    font-weight: 600;
    text-align: center;
    width: 80%;
    text-align: center;
    line-height: 50px;
}
.first-screen_cont{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
}
.first-screen_btn{
    color: #fff;
    border:2px solid #fff;
    font-size: 28px;
    padding: 15px;
    margin-top: 35px;
    cursor: pointer;
    border-radius: 5px;
    transition: 0.5s;
}
.first-screen_btn:hover{
    background-color: #850685;
    transition: 0.5s;
}