.login_input{
    width: 300px;
    height: 40px;
    border-radius: 5px;
    background-color: transparent;
    border:2px solid #fff;
    padding: 10px;
    color: #fff;
    font-size: 20px;
    outline: none;
    
}
.login_input::placeholder{
    color: #fff;
}
.login_container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
}
.login_header{
    color: #fff;
}

.login_btn{
    width: 200px;
    height: 50px;
    border: 2px solid #fff;
    display: flex;
    justify-content: center;
    color: #fff;
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    font-weight: 700;
    cursor: pointer;
}
.login_btn:hover{
    background-color: #850685;
}
.login_block{
    width: 600px;
    height: 400px;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}