.my-books_book-cont{
    width: 15%;
    height: 300px;
    border:3px solid #fff;
    color: #fff;
    font-size: 18px;
    font-weight: 800;
    justify-content: center;
    align-items: center;
    display: flex;
    margin-left: 15px;
    margin-top: 15px;
    border-radius: 10px;
    transition: 0.4s;
    cursor: pointer;

}
.my-books_book-cont:hover{
    background-color: #850685;
    transition: 0.4s;
}
.my-books_books-cont{
    display: flex;
    flex-wrap: wrap;
    height: 80vh;
    overflow-y: auto;
    margin-top: 15px;
  
    
}